.home-container {
  padding: 10px 120px;
  min-height: 100vh;
}

.loading-container {
  display: flex;
  flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}
.restaurants-container {
  padding: 30px 0;
}

.heading-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.heading-container span {
  display: block;
}

.heading {
  color: #183B56;
  font-family: "DM Sans";
  font-size: 32px;
  font-weight: 700;
  line-height: 48px;
  letter-spacing: 0px;
}

.description-home {
  color: #64748B;
  font-family: "DM Sans";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0px;
}

.filter {
  display: flex;
  align-items: center;
}

.dropdown {
  border: 0;
  background-color: transparent;
  outline: none;
  height: 30px;
  color: #475569;
}

.restaurants-list-container {
  margin: 30px 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.pagenation-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.arrow-button {
  background-color: transparent;
  border: 1px solid #000;
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.page-count {
  margin: 0 10px;
  color: #334155;
  font-family: "Bree Serif";
  font-size: 20px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: center;
}